<script>
  import mapboxgl from 'mapbox-gl';
  import {onMount} from "svelte";
  import { createEventDispatcher } from 'svelte';
  import MapDebug from "./MapDebug.svelte";
  import Marker from "./Marker.svelte";
  import ArrowsLayer from "./ArrowsLayer.svelte";
  import IntroLayer from "./IntroLayer.svelte";
  import {isMobile} from "./utils";
  import {cubicIn} from "svelte/easing";

	const dispatch = createEventDispatcher();

  export let data;
  export let settings;
  export let currentItem;

  let map = null;
  let markers = [];

  // Create map, and initialize the markers
  onMount(async () => {
    mapboxgl.accessToken = settings.token;
    map = new mapboxgl.Map({
      container: 'map',
      zoom: settings.zoom,
      center: settings.center,
      style: settings.style,
      attributionControl: false,
    });

    map.addControl(new mapboxgl.AttributionControl(), 'top-right');

    map.fitBounds(isMobile() ? settings.bounds.mobile : settings.bounds.desktop);
    map.resize();

    setTimeout(() => map.resize(), 500);
  });

  function changeCurrentItem(e) {
    const item = e.detail;
    dispatch('change', item);

    // On mobile, center to the item, so we can see the arrows
    if(isMobile()) {
      map.flyTo({
        center: item.marker,
        zoom: 7,
        speed: 0.2,
        curve: 1,
        easing: cubicIn,
      });
    }
  }

  export let arrowsLayer = null;
</script>

<svelte:options accessors={true} />

<div id="map"></div>

<MapDebug map={map} />

{#each data as item, index}
  <Marker map={map} item={item}
          currentItem={currentItem}
          on:change={changeCurrentItem} />
{/each}

<ArrowsLayer map={map} currentItem={currentItem} bind:this={arrowsLayer} />
<IntroLayer map={map} currentItem={currentItem} />


<style>
  #map {
    width: 100%;
    height: 100%;
  }

</style>