<script>
	import { data, settings } from './data.js';
	import Map from "./Map.svelte";
	import Panel from "./Panel.svelte";
	import {fixVH} from "./utils";
	import {onMount} from "svelte";
	import AboutPopup from "./AboutPopup.svelte";
	import SpeciesList from "./SpeciesList.svelte";

	export let currentItem = null;
	let hideMobileModel = true;
	let mapComponent;

	// Delete the static loader
	onMount(() => {
		document.getElementById('static-load').remove();
	});

	let isDebug = false;
	$: isDebug = window.location.hash.indexOf('#debug') !== -1;

	// Intro
	export let showIntro = false;
	function closeIntro() {
		showIntro = false
	}

	let panel;
	function setCurrentItem(e) {
		panel.focus();
		currentItem = e.detail;
	}

	let previousItem;
	$: if(currentItem && currentItem !== previousItem) {
		previousItem = currentItem;
		hideMobileModel = false;
	}

	let previousHideMobileModel = false;
	$: if(previousHideMobileModel !== hideMobileModel) {
		if(mapComponent) {
			mapComponent.arrowsLayer.animate();
		}

		previousHideMobileModel = hideMobileModel;
	}

	fixVH();
</script>


<main class="app" class:is-debug={isDebug}
									class:is-hide-model={hideMobileModel}>

	<section class="sr-only">
		<a href="#panel">Jump to content</a>
		<a href="#species-list__toggle">Jump to species list</a>
		<a href="#about__toggle">Jump to about us</a>
	</section>

	<SpeciesList
			currentItem={currentItem}
			on:change={setCurrentItem}
			data={data} />

	<Map data={data}
			 bind:this={mapComponent}
			 on:change={setCurrentItem}
			 currentItem={currentItem}
			 settings={settings} />

	<Panel currentItem={currentItem} bind:this={panel} />

	<aside class="sidebar">
		<a href="https://medliq.art" class="sidebar__link">
			<picture class="sidebar__logo">
				<source srcset="images/globe-logo-left.svg" media="(max-width: 764px)" />
				<source srcset="images/globe-logo-up.svg" media="(min-width: 765px)" />
				<img src="images/globe-logo-up.svg" alt="Back" />
			</picture>
		</a>

		<h1 class="sidebar__title">
			Flow of Invasion
		</h1>

		<AboutPopup text="{settings.aboutText}" />
	</aside>


	{#if currentItem}
		<button class="toggle-mobile-model"
						on:click={() => hideMobileModel = !hideMobileModel}>
			<span class="toggle-mobile-model__label">
				{#if !hideMobileModel}
					X Close
				{:else}
					View Art
				{/if}
			</span>
		</button>
		<button class="toggle-mobile-model toggle-mobile-model--overlay fill"
						on:click={() => hideMobileModel = !hideMobileModel}>
			<span class="visuallyhidden">Close</span>
		</button>
	{/if}
</main>
