/**
 * Convert line breaks to <br>s in text
 */
export function nlToBr(text) {
  if(text) {
    return text.replace(/\n/g, '<br>');
  }
}

/**
 * Get base path for images
 */
export function getBasePath(base = '') {
  return window.location.origin + '/' + window.location.pathname + '/images/' + base + '/';
}

/**
 * Are we in the mobile version?
 * Determine by the screen's width
 */
export function isMobile() {
  return window.innerWidth <= 765;
}

/**
 * Detect browser support for webp
 */
export function imgExt() {
  if(window.isWebpSupported === undefined) {
    const el = document.createElement('canvas');
    window.isWebpSupported = el.toDataURL('image/webp').indexOf('data:image/webp') === 0;
  }

  return window.isWebpSupported ? '.webp' : '.png';
}

/**
 * Modulo with support for negative numbers
 *
 * @param a
 * @param b
 * @return Number
 */
export function mod(a, b) {
  return ((a % b) + b) % b;
}

/**
 * Get the X coordinate from a touch or mouse event
 */
export function getX(e) {
  return e.offsetX || e.touches[0].clientX;
}

/**
 * VH fix for iOS, based on tips from https://css-tricks.com/the-trick-to-viewport-units-on-mobile/
 */
export function fixVH(ignoreResize = false) {
  const setVH = () => {
    const vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  };

  setVH();
  window.addEventListener('load', () => setVH());
  window.addEventListener('resize', () => {
    if(!ignoreResize) setVH()
  });
}

/**
 * A setInterval replacement with getAnimationFrame
 */
export function requestInterval(callback, delay) {
	let start = new Date().getTime();

	const loop = () => {
		const current = new Date().getTime();
		const delta = current - start;

		if(delta >= delay) {
			callback();
			start = new Date().getTime();
		}

		requestAnimationFrame(loop);
	}

	requestAnimationFrame(loop);
}