<script lang="ts">
  import {createEventDispatcher} from "svelte";
  import {fade} from 'svelte/transition';

  const dispatch = createEventDispatcher();

  export let currentItem;
  export let data = [];
  let show = false;
  let currentSlug;
  let orderedData = [];

  function setCurrentItem(e) {
    const foundItem = data.filter(x => x.slug == currentSlug)[0];
    dispatch('change', foundItem);
  }


  $: orderedData = data.sort((a, b) => {
    const A = a.species.toLowerCase();
    const B = b.species.toLowerCase();
    if (A < B) return -1;
    if (A > B) return 1;
    return 0;
  });

</script>


<section class="species-list">
  <button on:click={() => show = !show} aria-expanded="{show}"
          id="species-list__toggle"
          class="species-list__toggle"
          aria-label="List all species"
          aria-controls="species-list__popup">
    <svg class="icon icon--list"
         xmlns="http://www.w3.org/2000/svg"
         viewBox="0 0 17.25 14.65">
      <line x1="6.18" y1="1.95" x2="17.25" y2="1.95" fill="none" stroke="var(--icon-color)" stroke-miterlimit="10"/>
      <line x1="6.18" y1="7.49" x2="17.25" y2="7.49" fill="none" stroke="var(--icon-color)" stroke-miterlimit="10"/>
      <line x1="6.18" y1="13.03" x2="17.25" y2="13.03" fill="none" stroke="var(--icon-color)" stroke-miterlimit="10"/>
      <circle cx="1.75" cy="1.75" r="1.25" fill="none" stroke="var(--icon-color)" stroke-miterlimit="10"/>
      <circle cx="1.75" cy="7.36" r="1.25" fill="none" stroke="var(--icon-color)" stroke-miterlimit="10"/>
      <circle cx="1.75" cy="12.9" r="1.25" fill="none" stroke="var(--icon-color)" stroke-miterlimit="10"/>
    </svg>

    <span>
      Species
    </span>
  </button>

  {#if show}
    <form on:change={setCurrentItem}
          aria-labelledby="species-list__toggle"
          transition:fade={{ duration: 100 }}
          id="species-list__popup" class="species-list__popup">
      <ul class="species-list__items">
        {#each orderedData as item}
          <li class="species-list__item species-list-item__wrapper">
            <label class="species-list-item" on:mouseup={() => show = false}>
              <input class="species-list-item__radio"
                     bind:group={currentSlug}
                     checked={currentItem ? item.slug == currentItem.slug : false}
                     type="radio" value="{item.slug}"
                     name="species"/>


              <span class="species-list-item__image-wrapper">
                <img class="species-list-item__image fill fill-image"
                     src="images/markers/{item.slug}.png" alt=""/>
              </span>

              <span class="species-list-item__name">{item.name}</span>
              <span class="species-list-item__species">({item.species})</span>

            </label>
          </li>
        {/each}
      </ul>
    </form>
  {/if}
</section>