    images: { count: 155, width: 800, height: 489 },
    text: `Golden Wreath Wattle is a shrub or low tree, endemic to southwestern Australia, with a single or multiple trunks reaching to 2-6 m tall, but it can form a small tree 5 -9 m high, with a short but well-defined main stem. It is considered as one of the worst invasive alien plant species in the Mediterranean climatic regions. In our region, Golden Wreath Wattle grows in the Mediterranean climate, but also can penetrate to the edges of semi-arid areas where precipitation can be as low as 240 mm.  It grows in most types of soils, including sandy soils, and known to resist high salinity. Golden Wreath Wattle trees develop horizontal as well as vertical roots that reach different depths ranging from 3.5-16 m. Golden Wreath Wattle reproduce vegetative and by seeds, producing large numbers of seeds and the blooming period in our region extends between end of March and beginning of May. Golden Wreath Wattle was tested as a fodder for goats and sheep, however, the results showed that Golden Wreath Wattle cannot be used as an exclusive dietary for small ruminants but can be used as supplementary.
    
Golden Wreath Wattle was introduced to Israel and Palestine in the early years of the British Mandate (1920s) to stabilize sand dunes, afforestation, landscaping, and for its ability to thrive in harsh environments and saline soil. Only in the late 1970s, Golden Wreath Wattle has become an invasive species, however, the planting of this plant species continued at least until the 1980s.  

Some studies suggest that a common local ant species, Pheidole pallidula, plays a major role in dispersing Golden Wreath Wattles seeds, while exposure to fire triggers germination. Other studies from South Africa, showed that ants disperse Golden Wreath Wattles seeds to distances of 2-3 m and bury them 4-7 cm in the ground.

Golden Wreath Wattle affects native species and ecosystems in several ways. The trees create dense vegetation and large areas of shade, which terminate the growth of native species and displace them. It is known to produce a thick layer of dead leaves that accumulates beneath the trees, consequently, prevents germination of native species, increases soil nitrogen concentration, which creates favorable conditions for other alien invasive plant species. In dry habitats, dense stands of Golden Wreath Wattle were found to reduce soil moisture that is available for native species, particularly woody plants. Another negative effect of Golden Wreath Wattle on local ecosystems is the increase of fuel loads, thus, increasing the intensity of fire events.`
