    images: { count: 80, width: 626, height: 692 },
    text: `Asian tiger mosquitoes are fragile insects with slender bodies and an elongate proboscis, one pair of narrow wings, and three pairs of long, slender legs. The species is easily recognized by the bold black shiny scales and distinct silver white scales on the palps and tarsi. The scutum (back) is black with a distinguishing white stripe down the center beginning at the dorsal surface of the head and continuing along the thorax. Adults are known as tiger mosquitoes because of their noticeable patterns of very black bodies with white stripes. The adult mosquito can reach up to 10 mm in length, with a striking white and black pattern on the abdomen and thorax, the variation of the body size in adult mosquitoes depends on the density of the larval population and food supply in breeding water bodies. Tiger mosquitoes are native to the Oriental Region from the tropics of Southeast Asia, the Pacific and Indian Ocean Islands, north through China and Japan and west to Madagascar. Asian tiger mosquitoes are tree-hole mosquitos their breeding places in nature are small, restricted, shaded bodies of water in densely vegetated rural areas, but it colonizes many types of manmade sites and urban regions because of its ecological flexibility. The species is a nectarivores insect, obtaining needed energy and nutrients by feeding on flowering plants, but the females require mammalian blood to produce eggs, though will accept blood from a wide variety of hosts. Asian tiger mosquitoes were first documented in Israel during 2002, at the southern coastal plain where it started to expand its range to other areas. Most probably, it was brought through commerce and shipping- dormant eggs in tires. In 2012, it was documented for the first time in Palestine, specifically in Salfit district, since then it invaded other districts in Palestine. Asian tiger mosquito poses a potential threat to human health, as it shows the ability of  hosting  more than 20 arboviruses that could infect humans. It is also one of the possible vectors for dengue, chikungunya (CHIK), and Encephalitis viruses.`
