    images: { count: 98, width: 800, height: 774 },
    text: `Coypu is a large herbivorous rodent originally from South America, which lives partially in water. It weighs between 5 to 9 kg, has a 40-60 cm body length, and a tail up to 45 cm. Coypu or sometimes also called the Nutria lives in burrows alongside water bodies, and feeds on the stems of various aquatic plants. This rodent has distinct incisors that are bright orange in color and white marks on its muzzle.  Coypu can breed all year long, the female incubates the fetuses around 130 days, and can have up to 7 litters, multiple times a year.   Coypu lives near swamps, marches, streams, rivers, and lakes. 
    
    Coypu was introduced into our region in the 1950s from Chile. Coypu farms were established both in Kfar Masarky and Kfar Ruppin for the fur industry. However, some individuals escaped or were released into the wild in the 1970-1980, and reproduced in nature. Currently, feral populations of Coyou occupy most freshwater habitats in the Mediterranean region in Israel, even protected areas. Moreover, it succeeded to reach a single site in Palestine (Wadi Al Moqatta) in Jenin Governorate. 
    
    Coypu threatens our environment by creating a series of  burrows , which weakens the banks of rivers and streams , causes habitat destruction, and increases the risk of flooding.  Additionally, Coypu feeds on rhizomes and young plants which may lead to the breakdown of their communities, and decrease plant diversity. Coypu negatively affects breeding water birds (Moorhen, and Black-winged stilt) by damaging their nests, and competes with the Eurasian Otter.  Coypu causes damage to agriculture, fish ponds, wildlife, and human resources. Therefore, Coypu is included in the list of the 100 of the Worlds Worst Invasive Alien Species. 
    `
