    images: { count: 83, width: 800, height: 440 },
    text: `Red Palm weevil is a highly invasive pest of palm trees, native to southeast Asia, however, currently is invading many countries worldwide including several countries in Africa, Middle East, Europe, Australia, and even the United States. The adult beetles are relatively large, where females are 10 to 15% larger. The weevils color ranges from entirely orange-red to all black with all intermediates, depending on the number and size of black marks. It is a concealed tissue borer pest, documented to attack 17 different palm tree species worldwide. The weevils have a long, slender rostrum or snout which the female uses to penetrate palm tissue and create access wounds in which eggs are deposited. The female might lay the eggs in wounds, cracks, and crevices in the trunk, from the collar region near the roots, up to the base of frond petioles and axles near the crown of the palm. Females can lay 58-531 eggs, which hatch in about 1-6 days. Larvae that hatch from eggs, feed on the surrounding palm tissue and bore their way into the center of the palm.
    
    The red palm weevil was first documented in Israel and Palestine during the summer of 1999 on date palm plantations in the Jordan Valley, in the western bank of the Jordan River and in the northern area of the Dead Sea. Most probably the weevil came through imported date palm trees from other countries. In addition, since the female Red Palm weevil is capable of flying relatively long distances, it probably crossed from the eastern bank of the Jordan valley and Dead Sea area on the Jordanian side to Palestine (Jordan Valley and Dead Sea area). 
    
    Red palm weevil is a pest insect of date palm trees in our region. Its reproduction and life cycle leads to destruction and eventually killing the host tree. Therefore, infestations of red palm weevil have a tremendous impact on the economic produce of the palm (dates) and on the society as well, since date palm is closely associated with east Mediterranean  culture, religion and the life of the communities, in the region.`
