    images: { count: 93, width: 800, height: 572 },
    text: `The Common myna is a unique invader, although originally inhabits open woodland in south central and southeastern Asia, nevertheless currently is invading many countries worldwide with a wide range of habitats. The bird has a brown body, black hooded head, yellow patch behind the ear, and yellowish legs. It is a social and commensal bird, gathered in flocks during roosting and foraging time, a very vocal species all year-round, roost in large groups producing large amounts of droppings, and known to carry bird mites. Common mynas are not picky eaters (omnivorous), adapted extremely well to urban environments, living around humans in warm to hot climates.  
    
    This noisy bird can feed on plants such as different types of fruits, grains, flowers and their nectar, therefore considered as agricultural pests. They also prey on invertebrates, birds eggs and chicks, and tiny reptiles. It's even common for them to steal food from outdoor dining areas, or to feed on discarded waste from human habitation. The first record for a Common myna individual was in Eilat in 1987.  Thereafter, 10 years later, small groups were found in Park Hayarkon in Tel Aviv which probably escaped or were released.  
    
    The first documentation of breeding Common myna was during the year 2000, in the Tel Aviv Area. Since then, the species spread all over Israel and the Palestinian Territories, inhabiting urban and disturbed habitats. This bird species is believed to pair for life, extremely aggressive hollow-nesting species, that is, it nests and breeds in hollows found naturally in trees or artificially on buildings and other human infrastructures. They use the nest of other animals such as the Syrian woodpecker. The Common myna is one of the world's most invasive species and one of only three birds listed among "100 of the World's Worst Invasive Species" that pose a threat to biodiversity, agriculture and human interests. `
