    slug: 'rose',
    images: { count: 72, width: 794, height: 798 },
    text: `The Rose-ringed parakeet is a medium size parrot originally native to central Africa and south Asia, but currently is spread in many countries worldwide including Israel/ Palestine.  The parakeets build their nests and breed in cavities inside trees/ buildings, and are very social. Usually, Rose-ringed parakeets cluster at night for roosting, and feed on fruits, seeds, and occasionally on vegetables, flowers, and nectars. They are very noisy birds, and can be taught to speak in captivity. Their average lifespan ranges from 30-34 years. 
      
      The Rose-ringed parakeets were unintentionally introduced to Israel as some individuals, which were brought from Iran as cagebirds, escaped from captivity during the 70s, and eventually spread in the region. The rose-ringed parakeets live in a wide range of habitats including orchards, woodlands, agriculture areas, and gardens. This species has successfully adapted to living in disturbed habitats as well. 
      
      The Rose-ringed parakeet is currently widespread in Israel, the West Bank and Gaza Strip. Flocks of this species can be seen in urban and cultivated areas in all climatic regions. Larger numbers are found in lower lying areas.    
      
      The Rose-ringed parakeet is negatively affecting our environment, and is considered as one of the worst bird invasive species causing damage. The bird is an agricultural pest; causing harm to cereal and fruit crops. Moreover, the Rose-ringed parakeet competes with other birds on food, and nesting sites such as Hoopoe and Syrian woodpecker.  Furthermore, the bird can assist in spreading invasive plant seeds. Finally, the Rose-ringed parakeet acts as a reservoir and vector for various human, wildlife, and livestock diseases. 
    `
