    images: { count: 215, width: 800, height: 481 },
    text: `The Monk Parakeet is a small to medium size parakeet species, native to subtropical and temperate South America, including Bolivia, Paraguay, Uruguay, southern Brazil south to the Patagonia region of Argentina. The species was introduced to many countries worldwide (e.g., Eastern United States, southern Canada, and several countries in Europe, Asia, and the Middle East). The Monk Parakeet usually is found in loose flocks of 15-20 birds, and in some cases can reach up to100 birds. This bird is relatively vocal with a wide type of screeching, squawks and chattering. The bird inhabits open habitats, ranging from savannah woodlands, farmlands, plantations, orchards, and cultivated forest. This species of parakeet is the only one that build its own nest, which allow them to adapt to colder environments and urban areas in their invaded regions. Within its native range, the Monk parakeet is considered as an agricultural pest, as it causes severe damages to field crops and orchards. In addition, since this parakeet build their own nests (communal stick nesters); they are reported to cause transmission lines short-circuited by nesting birds and damage human infrastructure. In their native range, the Monk parakeet are generalist granivores, feeds on maize, millet, sorghum, sunflowers and other seeds. They are also recorded feeding on fruits, nuts, berries and insects, but they are flexible in their diet.  
    
      In our region, the Monk parakeet became a popular pet bird in the mid-1900. It was first observed in the wild in Tel-Aviv district during 1995, but the first record of breeding birds was later in 1998, in the same area. The Monk parakeet population size in Israel was estimated to be between 1162-1943 in 2015, while the number of chambers reached 1213. The nesting sites in Israel and Palestine were found to be divided between urban habitat, agricultural, and semi-agricultural habitats.  The nests were located at five different types of trees, namely; Aleppo pines <em>Pinus halepensis</em>, Date palms <em>Phoenix spp., Eucalyptus spp.</em>, Canary pines <em>Pinus canariensis</em>, and Fan palms <em>Washingtonia spp</em>.
       
      The population growth rate of the Monk parakeet in Israel is increasing exponentially, three times higher than the reported rate in the United State. This is a very serious issue, as future population and range increase is expected to be very significant. It is documented to cause damage to human infrastructure, especially in its invaded areas (e.g., electrical substations, transmission lines, distribution lines, pylons, communication towers), as this species of parakeet nests in colonies, building a single, large bulky nests that have separate entrances (chambers) for each pair of birds. In addition, the Monk parakeet might transmit plant diseases, by transporting infected planting materials to uninfected trees, such as citrus cancer, which is a major concern, for example in Florida. Researchers suggested that Monk parakeets might spread Newcastle disease as well. 
    `
