    text: `Devil Firefish, is a species of ray-finned fish native to the western Indo-Pacific region. It ranges from South Africa, along East Africa to the Red Sea, and along the coast of southern Asia to India and Indonesia. Its scientific name is from Greek pteron, meaning "wing", and Latin miles, meaning "soldier". Devil Firefish is a carnivorous fish, and documented as invasive fish species in many countries including the Middle East. This species of fish has distinctive brown or maroon, and white stripes or bands covering the head and body. They have fleshy tentacles above their eyes and below the mouth; fan-like pectoral fins; long, separated dorsal spines; 13 dorsal spines; 10-11 dorsal soft rays; 3 anal spines; and 6-7 anal soft rays. An adult lionfish can grow as large as 18 inches. Devil Firefish inhabit natural and artificial structures at depths from just a few inches of water to over 300 m.  In the invaded range, lionfishes have been found in a variety of habitats, including reefs, wrecks, bridge pilings, seagrass and natural hardbottom. Lionfish are slow-moving and conspicuous, so they must rely on their unusual coloration and fins to discourage would-be predators from eating them. The devil fish is mainly nocturnal and hides in crevices during the daytime. It feeds on fish and small crustaceans. It has few predators, probably because of its venomous spines, but larger Devil fish do prey on smaller ones. Their fin spines are highly venomous and have caused death to humans in some reported cases. Despite this, a sting from this species is rarely fatal to humans, but a sting can cause extreme pain, vomiting, convulsion, minor paralysis, and breathing difficulties.
    
    The invasive fish has been spotted along the length of Palestinians and Israels Mediterranean coast, particularly in rocky areas, generally several hundred meters away from the shore. It made its way from its home in the Red Sea into the Mediterranean via the Suez Canal. Devil Firefish was first spotted in the Mediterranean in 2012, and has been considered as invasive fish species since then. It is carnivorous and poisonous, and can kill and eat a large number of native fish along the shore of Israel and Palestine in the rocky areas of the Mediterranean Sea, and endangers people if they get stings of its poisonous spikes. `
