    text: `Marbled spinefoot rabbitfish is a gregarious, herbivorous ray-finned fish, native throughout the Red Sea and the western Indian Ocean. It occurs in shallow waters near to or just below the shore rocks, and other hard substrata, often covered by algae. It is often found close to the bottom in depths not exceeding 60 m as well as in the vicinity of coral reefs. The juveniles prefer to feed at high tide on algae-covered shallow rocky platforms. The Marbled spinefoot rabbitfish is a diurnal fish which hides in reef crevices at night and grazes over reefs during the day.
    
      Marbled spinefoot rabbitfish have a laterally compressed body, usually growing to about 20-30 cm. Its dorsal fin has 13 spines and 10 soft rays, the anal fin has 7 spines and 9 soft rays, and the caudal fin is forked. The slender spines are barbed and bear venom, poisonous spines help it to avoid severe predation pressures, and it also can lose its color at nightfall and change color if threatened.
      
      Its color is variable and it usually shows some dark spots and yellow wavy lines along the sides. Its upper body tends to be gray-green to brown with a yellow abdomen or silvery white underside. This rabbitfish has venom glands associated with the spines in the fins, its sting is very painful. It is documented that the marbled spinefoot rabbitfish is a source of ciguatoxin-like toxins and causes hallucinations when consumed. The construction of the Suez Canal in Egypt has led to Lessepsian migration and natural dispersal of this species into the Mediterranean Sea. The species has been reported as invasive species in several countries within the mediterranean sea including Palestine, Israel, Lebanon, Syria, Cyprus, Turkey Greece, Libya, and Croatia.  
          `
