<script>
  import { settings } from './data.js';
  import {getBasePath, isMobile} from "./utils";

  export let map;
  export let currentItem;

  $: if(map && !isMobile()) {
    map.on('load', () => {
      map.addSource('intro', {
        type: 'image',
        url:  getBasePath('arrows') + 'intro.png',
        coordinates: settings.introLayerPosition
      });

      map.addLayer({
        id: 'intro-layer',
        type: 'raster',
        source: 'intro',
        'paint': {
          'raster-fade-duration': 0
        }
      });
    });
  }

  $: if(currentItem && map && map.getLayer('intro-layer')) {
    map.setLayoutProperty('intro-layer', 'visibility', 'none');
  }

</script>