<script>
  import { createEventDispatcher } from 'svelte';
  import mapboxgl from "mapbox-gl";
  import {getBasePath, imgExt} from "./utils";

  const dispatch = createEventDispatcher();

  export let item;
  export let map;
  export let currentItem;
  export let autoCenter = false;

  let self = null;
  let loaded = false;

  function setCurrentItem() {
    dispatch('change', item);
  }

  function preloadArrows() {
    const basePath = getBasePath('arrows') + item.slug;
    if(item.arrows && item.arrows.frameCount) {
      for(let i = 1; i <= item.arrows.frameCount; i++) {
        const img = new Image();
        img.src = basePath + '/' + i + imgExt();
      }
    }
  }


  let zoom = 'medium';
  function onMapZoom() {
    const adjustZoom = () => {
      const zoomLevel = map.getZoom();
      if(zoomLevel >= 8.7)
        zoom = 'large';
      else if(zoomLevel <= 7.5)
        zoom = 'small';
      else
        zoom = 'medium';
    };

    map.on('zoom', (e) => {
      adjustZoom();
    });

    adjustZoom();
  }

  $: if(map) {
    map.on('load', () => {
      new mapboxgl.Marker(self).setLngLat(item.marker).addTo(map);
      loaded = true;

      onMapZoom();
    });
  }



  let isCurrent = false;
  $: {
    isCurrent = item && currentItem && (item.slug === currentItem.slug);
  }
  $: if(isCurrent) {
    if(autoCenter) {
      map.flyTo({
        center: item.marker,
        speed: 0.3,
        curve: 1,
        easing: (x) => 1 - Math.pow(1 - x, 2),
        essential: true,
      });
    }
  }
</script>

<button class="map__marker marker"
     data-zoom="{zoom}"
     aria-label="{item.name} ({item.species})"
     class:is-current={isCurrent}
     class:is-loaded={loaded}
     bind:this={self}
     on:mouseenter={preloadArrows}
     on:click={setCurrentItem}>
  <img class="marker__image" src="images/markers/{item.slug}.png" alt="">
  <img class="marker__overlay" src="images/markers/overlays/{item.slug}.svg" alt="">
</button>

<style>
  .marker__image { width: 100%; height: 100%; object-fit: cover; }
</style>