<script>
  import SimpleBar from "@woden/svelte-simplebar";
  import {settings} from "./data";
  import ModelViewer from "./ModelViewer.svelte";
  import {nlToBr} from "./utils";

  export let currentItem;
  let wrapper;

  export function focus() {
    if(wrapper) wrapper.focus();
  }

</script>

<section class="panel" id="panel"
         aria-labelledby="species__name"
         bind:this={wrapper} tabindex="0"
         class:is-intro={!currentItem}>
  {#if currentItem}
    <SimpleBar class="panel__scroll" direction="rtl" autoHide={false}>
      <div class="panel__info">
          <h2 class="panel__title species__name" id="species__name">{currentItem.name}</h2>

          {#if currentItem.species}
            <div class="species__species">{currentItem.species}</div>
          {/if}

          <img class="species__map" src="images/maps/{currentItem.slug}.svg" alt="">

          {#if currentItem.text}
            <div class="panel__text">{@html nlToBr(currentItem.text)}</div>
          {/if}
      </div>
    </SimpleBar>
  {:else}
    <div class="panel__info panel__info--intro intro">
      <h1 class="panel__title intro__title">Introduction</h1>
      <div class="panel__text intro__text">{@html nlToBr(settings.introText)}</div>
    </div>

    <img class="intro__bg" src="images/intro-bg.jpg" alt="">
  {/if}


  {#if currentItem}
    <ModelViewer currentItem={currentItem} />
  {/if}

</section>
