<script>
  import { fly, fade } from 'svelte/transition';
  import {isMobile} from "./utils";

  let open = false;
  let textElement;
  let button;
  export let text = '';

  const transition = isMobile() ? { y: 100 } : { x: 50 };

  function onKeyUp(e) {
    if(e.key.toLowerCase() === 'escape') open = false;
  }

  $: if(open && textElement) textElement.focus();

</script>

<button class="about__toggle"
        id="about__toggle"
        bind:this={button}
        aria-expanded={open}
        aria-controls="about"
        on:click={() => open = !open}>
  <img src="images/info.svg" alt="About the project" />
</button>

{#if open}
<section class="about" id="about"
         on:keyup={onKeyUp}
         transition:fly="{transition}">
  <div class="about__inner" bind:this={textElement} tabindex="0">
    {@html text}
  </div>

  <button class="about__close"
          on:click={() => open = false}>
    <img src="images/close.svg"
         width="30" height="30" alt="Close">
  </button>
</section>
{/if}