<script>
  import {getBasePath, imgExt, requestInterval} from "./utils";
  import {quadInOut} from 'svelte/easing';
  import {onMount} from "svelte";

  export let map;
  export let currentItem;

  let layer = null;
  let loaded = false;

  const basePath = getBasePath('arrows');

  /**
   * Animation loop
   */
  let currentFrame = 1;
  let playing = false;

  onMount(() => {
    requestInterval(() => {
      if(!map) return;

      if (currentItem && currentItem.arrows && (currentFrame <= currentItem.arrows.frameCount) && playing) {
        updateFrame(currentFrame++);
      }
      else {
        playing = false;
      }
    }, 50);
  });

  const onSourceData  = () => {
    //setLayerVisibility('visible');
  }

  export function animate() {
    console.log('called animate');
    currentFrame = 1;
  }

  function createLayer() {
    if (currentItem && currentItem.arrows) {
      map.addSource('arrows', {
        type: 'image',
        url: basePath + currentItem.slug + '/1' + imgExt(),
        coordinates: currentItem.arrows.position
      });

      map.addLayer({
        id: 'arrows-layer',
        type: 'raster',
        source: 'arrows',
        'paint': {
          'raster-fade-duration': 0
        }
      });
    }
  }

  function setLayerVisibility(visibility) {
    if (!map || !map.getLayer) return;
    const layer = map.getLayer('arrows-layer');
    if (layer) {
      map.setLayoutProperty('arrows-layer', 'visibility', visibility);
    }
  }

  function updateFrame(frame) {
    const url = basePath + currentItem.slug + '/' + frame + imgExt();
    const arrowsSource = map.getSource('arrows');
    if(!arrowsSource) return;

    arrowsSource.updateImage({
      url: url,
      coordinates: currentItem.arrows.position
    });

    map.once('sourcedata', () => setLayerVisibility('visible'));
  }

  function fitMapToArrowsBounds(duration = 1000) {
    return new Promise((resolve, reject) => {
      if (currentItem.arrows) {
        const position = [
          currentItem.arrows.position[0],
          currentItem.arrows.position[2],
        ];

        map.on('moveend', () => {
          resolve()
        });

        map.fitBounds(position, {
          duration: duration,
          animate: true,
          easing: quadInOut,
          essential: true,
        });
      }
      else {
        resolve();
      }
    });
  }

  async function animateAndFly() {
    currentFrame = 1;
    setLayerVisibility('none');
    await fitMapToArrowsBounds();
    playing = true;
  }

  $: if(map) map.on('sourcedata', () => onSourceData());

  $: if (currentItem && map && currentItem.arrows) {
    if (map && !map.getLayer('arrows-layer')) createLayer();
    console.log({currentItem, map, arrows: currentItem.arrows});
    animateAndFly();
  } else {
    if (map) map.off('sourcedata', onSourceData);
    setLayerVisibility('none')
  }

</script>