<script>
  import {imgExt, mod, getX, getBasePath} from "./utils";
  import {onMount} from "svelte";

  export let currentItem;
  let lastItem;

  let canvas;
  let position = 1;
  let images = {};
  let imagesLoaded = {};
  let currentImagesLoaded = 0;
  let currentImagesCount = 0;
  let progress = 0;

  let context = null;

  function debugMode() {
    return window.location.hash === '#debug';
  }

  const basePath = getBasePath('arrows');
  onMount(() => {
    context = canvas.getContext('2d');
  });

  function clear() {
    context.clearRect(0, 0, context.canvas.width, context.canvas.height);
  }

  let lastLoadedItem = null;

  function loadImages() {
    const slug = currentItem.slug;

    if(debugMode())
      console.log('loading images for', slug);

    const itemDidntChange = currentItem === lastLoadedItem;
    const imagesAlreadyLoaded = imagesLoaded[slug] === currentItem.images.count;
    const imageArrayExists = images[slug] && images[slug].length;
    if (itemDidntChange || imagesAlreadyLoaded) return;

    if(debugMode())
      console.log({itemDidntChange, imagesAlreadyLoaded}, imagesLoaded[slug], currentItem.images.count);

    images[slug] = [];
    imagesLoaded[slug] = 0;

    // If the last item didn't finish loading, clear it,
    // so we won't wait until it finishes
    const lastItemDidntFinishLoading = lastLoadedItem &&
        imagesLoaded[lastLoadedItem.slug] < lastLoadedItem.images.count;

    if (lastItemDidntFinishLoading) {
      images[lastLoadedItem.slug].forEach((image, i) => {
        images[lastLoadedItem.slug][i].src = '';
      });

      images[lastLoadedItem.slug] = [];

      console.warn(`Item ${lastLoadedItem.slug} didn't finish loading. Deleted images.`);
    }

    // Create images and load them
    for (let i = 1; i <= currentItem.images.count; i++) {
      const image = new Image();
      images[slug].push(image);
      image.onload = function () {
        imagesLoaded[slug]++;
      }
      image.src = getBasePath('model') + slug + '/' + i + imgExt();
    }

    // If loading is stuck at a large percentage after a few seconds, declare it loaded
    const createForceLoadTimeout = (timeout) => {
      setTimeout(() => {
        const loadedRatio = imagesLoaded[slug] / currentItem.images.count;
        if (loadedRatio > 0.75) {
          imagesLoaded[slug] = currentItem.images.count;

          if(debugMode())
            console.log(`Force load after ${timeout} seconds`);
        } else {
          if(debugMode())
            console.log(`Not loaded after ${timeout} seconds: ${Math.round(loadedRatio * 100)}%`);
        }
      }, timeout * 1000);
    };

    const forceLoadMilestones = [5, 10, 15, 20, 30];
    forceLoadMilestones.forEach(i => createForceLoadTimeout(i));

    lastLoadedItem = currentItem;
  }

  $: if (currentItem) {
    currentImagesCount = currentItem.images.count;
    currentImagesLoaded = imagesLoaded[currentItem.slug];
  }
  $: progress = currentImagesLoaded / currentImagesCount;
  $: isLoading = currentImagesLoaded < currentImagesCount;

  function drawImage() {
    if (images && images[currentItem.slug] && images[currentItem.slug][position] && context) {
      clear();

      let width = context.canvas.width;
      let height = context.canvas.height;

      if (canvas.offsetWidth > currentItem.images.width) {
        const ratio = currentItem.images.width / currentItem.images.height;
        width = currentItem.images.width;
        height = currentItem.images.width * ratio;
      }

      context.drawImage(images[currentItem.slug][position], 0, 0, width, height);
    }
  }

  let drawImageInterval = null;
  $: if (context && currentItem) drawImage()

  $: if (currentItem && currentItem !== lastItem) {
    position = 1;
    lastItem = currentItem;
    loadImages();
    drawImage();
    drawImageInterval = setInterval(() => drawImage(), 20);
  }

  // Controller
  function increasePosition(amount = 1) {
    if (position <= currentItem.images.count)
      position += amount;
    else
      position = 1;
  }

  function decreasePosition(amount = 1) {
    if (position >= 1)
      position -= amount;
    else
      position = currentItem.images.count;
  }

  // Mouse drag
  let originalOffset = 0;
  let isMouseDown = false;
  let originalPosition = 0;

  function onMouseDown(e) {
    isMouseDown = true;
    originalPosition = position;
    originalOffset = getX(e);
  }

  function onMouseUp(e) {
    originalOffset = 0;
    isMouseDown = false;
  }

  function onMouseMove(e) {
    if (isMouseDown) {
      const dX = Math.floor((getX(e) - originalOffset) / context.canvas.width * currentItem.images.count);
      let newPosition = originalPosition - dX;

      if (newPosition >= 1 && newPosition <= currentItem.images.count)
        position = newPosition;

    }
  }
</script>

<svelte:window on:mouseup={onMouseUp}/>

<div class="model-viewer"
     class:is-loading={isLoading}
     class:is-mouse-down={isMouseDown}>
  <canvas class="model-viewer__canvas"
          on:mousedown={onMouseDown}
          on:touchstart={onMouseDown}
          on:mouseup={onMouseUp}
          on:touchend={onMouseUp}
          on:mousemove={onMouseMove}
          on:touchmove={onMouseMove}
          width="{currentItem.images.width}"
          height="{currentItem.images.height}"
          bind:this={canvas}></canvas>

  {#if !currentImagesLoaded || !currentImagesCount || currentImagesLoaded < currentImagesCount}
    <div class="model__loading loading">
      <div class="loading__title">Loading...</div>
      <progress class="loading__progress"
                max="{currentImagesCount || 2}"
                value="{currentImagesLoaded || 100}">
        {progress * 100}%
      </progress>
    </div>
  {/if}

  <div class="controller model-viewer__controller"
       class:is-loaded={ currentImagesLoaded >= currentImagesCount }>
    <button class="controller__left" aria-label="Rotate art left"
            on:mousedown={() => increasePosition(1)}>
      <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
           x="0px" y="0px" viewBox="0 0 36.3 36.3" overflow="visible" xml:space="preserve">
        <g>
          <g>
            <circle fill="var(--icon-fill)" cx="18.2" cy="18.2" r="17.2"/>
            <path fill="var(--icon-stroke)"
                  d="M18.2,0.4v1c9.2,0,16.7,7.5,16.7,16.7c0,9.2-7.5,16.7-16.7,16.7S1.4,27.4,1.4,18.2S8.9,1.4,18.2,1.4V0.4 M18.2,0.4C8.4,0.4,0.4,8.4,0.4,18.2s7.9,17.7,17.7,17.7s17.7-7.9,17.7-17.7S27.9,0.4,18.2,0.4L18.2,0.4z"/>
          </g>
          <polyline fill="none" stroke="var(--icon-stroke)"
                    stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="10"
                    points="21.1,11.8 13.7,18.3 21.1,24.4"/>
        </g>
      </svg>
    </button>

    <div class="controller-range">
      <input class="controller__range controller-range__range"
             bind:value={position}
             type="range" min="1" max="{currentItem.images.count}"/>

      <div class="controller-range__notches">
        {#each Array(17) as i}
          <div class="controller-range__notch"></div>
        {/each}
      </div>
    </div>

    <button class="controller__right"
            aria-label="Rotate art right"
            on:mousedown={() => decreasePosition(1)}>
      <svg xmlns="http://www.w3.org/2000/svg"
           xmlns:xlink="http://www.w3.org/1999/xlink"
           x="0px" y="0px" viewBox="0 0 36.3 36.3" overflow="visible" xml:space="preserve">
      <g>
        <g>
          <circle fill="var(--icon-fill)" cx="18.2" cy="18.2" r="17.2"/>
          <path fill="var(--icon-stroke)"
                d="M18.2,0.4v1c9.2,0,16.7,7.5,16.7,16.7c0,9.2-7.5,16.7-16.7,16.7S1.4,27.4,1.4,18.2S8.9,1.4,18.2,1.4V0.4 M18.2,0.4C8.4,0.4,0.4,8.4,0.4,18.2s7.9,17.7,17.7,17.7s17.7-7.9,17.7-17.7S27.9,0.4,18.2,0.4L18.2,0.4z"/>
        </g>
        <polyline fill="none" stroke="var(--icon-stroke)" stroke-linecap="round" stroke-linejoin="round"
                  stroke-miterlimit="10"
                  points="15.2,24.5
          22.6,18 15.2,11.9 	"/>
      </g>
      </svg>


    </button>
  </div>

</div>